.spinner {
  position: absolute;
  display: flex;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  background-color: white;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.rezervacija {
  margin-bottom: 200px;
}
footer {
  background: #eff1f5;
  box-shadow: 0 -1px 2px 0 black;
  min-height: 132px;
  text-align: center;
  padding: 32px;
}
footer .logo {
  margin: 0 auto 10px auto;
}
body {
  background-color: rgba(0, 0, 0, 0.28);
  background-repeat: no-repeat;
  background-size: cover;
}
.navbar-nav {
  flex-direction: unset !important;
}
.footer {
  background-color: #bfaa72 !important;
  margin-top: 55px;
}
::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-thumb {
  background: #ddd;
}

::-webkit-scrollbar-track {
  background: #666;
}
.navbar .router-link-exact-active {
  color: #041433;
  border-bottom: 3px solid #80734f;
}
.navbar-light .navbar-nav .nav-link {
  color: black !important;
}
.navbar-nav .nav-link {
  color: #041433;
  box-sizing: border-box;
  margin: 0 40px 0 0;
  font-weight: 400;
}
.navbar {
  background-color: #bfaa72 !important;
  box-shadow: 0 1px 3px 0 black;
}
.btn-primary {
  background-color: #3b6b35 !important;
  border: 0px solid black !important;
}
.btn-danger {
  border: 0px solid black !important;
  background-color: #4c4a41 !important;
}
.btn-danger:active,
.btn-danger:focus {
  outline: none !important;
  box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 0.75) !important;
}
.btn-primary:active,
.btn-primary:focus {
  outline: none !important;
  box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 0.75) !important;
}
@media (min-width: 576px) {
  .container {
    max-width: inherit;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}
@media (max-width: 767px) {
  .nav-item {
    margin-bottom: 1rem;
  }
  .nav-item .nav-link {
    padding: 0 0 0 5px;
  }
  .nav-item .nav-link.router-link-exact-active {
    border-bottom: 0;
    border-left: 3px solid #80734f;
    padding-left: 13px !important;
    vertical-align: middle;
  }
  .navbar-brand {
    margin-left: 2.5rem;
    vertical-align: top;
  }
  .navbar-toggler {
    margin-right: 2.5rem;
  }
  .navbar-nav {
    margin-left: 1.5rem;
    margin-right: 1.5rem;
  }
  .navbar-nav:first-child {
    margin-top: 1.5em;
  }
  .navbar-nav:last-child {
  }
  .navbar-nav:last-child li {
    margin-bottom: 1em;
  }
  .navbar-nav:last-child li .icon {
    margin-right: 1em;
    vertical-align: middle;
  }
  .navbar-nav:last-child li a {
    vertical-align: middle;
    color: #041433;
  }
  .navbar-nav .user-info img {
    margin-right: 1em;
  }
  .navbar-nav .btn-link {
    padding: 0;
    color: #7ea0e4;
    min-width: 0;
  }
}
@media only screen and (min-width: 768px) {
  .result-block {
    min-width: 700px;
  }
}
html {
  font-size: 16px;
}
body {
  color: black;
}
body,
html {
  height: 100%;
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
}
h6.muted {
  color: #6c757d;
  text-transform: uppercase;
  font-weight: 300;
  margin-bottom: 1.5rem;
  font-size: 1rem;
}
